import { IImageData } from './IImageData';

export interface IVideoData {
    _links?: IVideoFileDataLinks;
    playTime?: number;
    subtitle?: string;
    thumbnail?: IImageData;
    title?: string;
    description?: string;
    interactiveTriggersEnabled?: boolean;
    minimumAge?: number;
    id?: string;
    locale?: string;
}

/**
 * cms datatype of VideoFileDataLinks
 */
export interface IVideoFileDataLinks {
    binaryReferences?: IBinaryReference[];
}

/**
 * cms datatype of BinaryReference
 */
export interface IBinaryReference {
    format?: string;
    bitrate?: number;
    clientHref?: string;
    height?: number;
    width?: number;
    alias?: string;
    contentType?: string;
    description?: string;
    extension?: string;
    sizeInBytes?: number;
    sourceHref?: string;
    href?: string;
}
